@import "sass/global_styles/bootstrap-classes";

@import "sass/global_styles/global";

@import "sass/global_styles/header";

@import "sass/global_styles/footer";

// @import "sass/commonSections/homepage-hero";

// GLOBAL STYLES

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-weight: normal;
  font-style: normal;
  position: relative;
  overflow-x: hidden;
  color: #000;
  background-color: #fff;
  font-family: "Cairo", sans-serif;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #474c4d;
}

li {
  list-style: none;
  padding: 0;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
    position: relative;
  }
}

@media #{$mobile} {
  .container {
    padding: 0 20px !important;
    position: relative;
  }
}

.hero-section {
  padding: 150px 0 100px;
  @media #{$maxTab} {
    padding: 60px 0;
  }
  @media (min-width: 1200px) and (min-height: 601px) {
    height: calc(100vh - 32px);
    display: flex;
    align-items: center;
  }
  @media (min-height: 1000px) {
    height: auto;
    padding: 135px 0;
  }
  @media (max-height: 600px) {
    height: auto;
    padding: 80px 0;
  }
}

.flex-shrink-0 {
  flex-shrink: 0;
}

button:focus,
input:focus {
  outline: none;
  box-shadow: none;
}

// MAIN NAVIGATION
.homepage-header {
  background-image: url(../imgs/homepage-hero-bg.png);
  height: 100vh;
  background-color: #ecfbff;
}
.navigation {
  padding: 34px 0;
  .main-logo:focus,
  .main-logo:link {
    font-weight: 700;
    font-size: 20px;
    line-height: 37px;
  }

  .contact-link {
    font-weight: 600;
    font-size: 15px;
    line-height: 28px;
  }

  .language-dropdown {
    background: #fff;
    border-radius: 7px;
    padding: 10px 12px;
    border: 0;
    font-weight: 600;
    font-size: 15px;
    line-height: 28px;
  }

  .login {
    background: #2c2ffe;
    border-radius: 7px;
    font-weight: 700;
    font-size: 15px;
    line-height: 28px;
    color: #ffffff;
    padding: 10px 20px;
  }
}

.homepage-hero-image {
  display: flex;
  height: calc(100% - 116px);
  align-items: center;
  h1 {
    font-weight: 700;
    font-size: 56px;
    line-height: 76px;
    margin-bottom: 36px;
  }
  .form-control {
    display: block;
    width: 100%;
    padding: 15px 19px;
    line-height: 1.4;
    border: 0;
    font-size: 18px;
    line-height: 32px;
    background: none;
    &::placeholder {
      color: #777c80;
    }
  }
  .form-control:focus {
    color: #000;
    outline: 0;
  }
  .intl-tel-input,
  .iti {
    width: 100%;
  }

  .iti--separate-dial-code .iti__selected-dial-code {
    color: $primaryColor;
    font-weight: 700;
  }

  .form-group {
    background: #ffffff;
    box-shadow: 0px 12px 50px rgba(76, 76, 108, 0.15);
    border-radius: 79px;
    padding: 8px;
    max-width: 480px;
  }

  .search-btn {
    background: #2c2ffe;
    border-radius: 74px;
    padding: 14px 17px;
    font-weight: 700;
    font-size: 15px;
    line-height: 28px;
    color: #fff;
    border: 0;
    cursor: pointer;
  }

  .iti--separate-dial-code .iti__selected-flag {
    background: #f0f5f7;
    border-radius: 105px;
    padding: 8px;
  }

  .iti--allow-dropdown input,
  .iti--allow-dropdown input[type="text"],
  .iti--allow-dropdown input[type="tel"],
  .iti--separate-dial-code input,
  .iti--separate-dial-code input[type="text"],
  .iti--separate-dial-code input[type="tel"] {
    margin-left: 10px;
  }

  .app-btns {
    a {
      transition: 0.3s;
      &:hover {
        opacity: 0.8;
      }
    }
  }
}

.section {
  padding: 100px 0;
}

.section-title {
  font-weight: 700;
  font-size: 48px;
  line-height: 58px;
}

// App Features
.app-features {
  .feat-box {
    background: #ffffff;
    box-shadow: 0px 33px 90px rgba(76, 76, 108, 0.1);
    border-radius: 25px;
    padding: 48px 32px;
    h3 {
      font-weight: 700;
      font-size: 28px;
      line-height: 38px;
    }
    p {
      font-size: 20px;
      line-height: 30px;
    }
  }
}

// Contact
.contact-widget,.contact-details {
  h3 {
    font-weight: 700;
    font-size: 48px;
    line-height: 58px;
  }
  p {
    font-weight: 400;
    font-size: 24px;
    line-height: 38px;
  }

  .contact-widget-content {
    position: relative;
    &:after {
      content: "";
      position: absolute;
      background-image: url(../imgs/contact-widget-bg.png);
      background-color: #fcf3eb;
      border-radius: 85px;
      width: 82%;
      height: 81%;
      top: -20px;
      z-index: -1;
      left: 11%;
    }
  }
  .contact-details-content {
    position: relative;
    &:after {
      content: "";
      position: absolute;
      background-image: url(../imgs/contact-details-bg.png);
      background-color: #ECFBFF;
      border-radius: 85px;
      width: 82%;
      height: 81%;
      top: 45px;
      z-index: -1;
      left: 11%;
    }
  }
}

.contact-details {
  padding-top: 0;
}